/* Project Area */

.filterportfolio{
    .thumb {
        a {
            img {
                -webkit-filter: grayscale(100%); /* Safari 6.0 - 9.0 */
                filter: grayscale(100%);
                @extend %transition;
            }
        }
    }
    &:hover{
        .thumb {
            a {
                img {
                    -webkit-filter: grayscale(0); /* Safari 6.0 - 9.0 */
                    filter: grayscale(0);
                }
            }
        }
    }
}
